// token
const setTokenStorage = (token) => {
  uni.setStorageSync('am_token', token)
}

// token
const getTokenStorage = () => {
  let token = ''
  try {
    token = uni.getStorageSync('am_token')
  } catch (e) {
  }
  return token
}

// config
const configHandle = (config) => {
  config.header = {
    ...config.header,
    token: getTokenStorage() // token header
  }
  return config
}

export {
  setTokenStorage,
  getTokenStorage,
  configHandle
}
